        text-align: center;
        font-weight: normal;
        font-size: 2vw;
        align-items: center;
        outline: none
    }

    .button-label {
        color: #FFFFFF;
    }

    .label {
        color: #6B737C;
    }

    .comment-block {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: 40%;
